* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Hind', sans-serif;
}

html {
  background-color: darkslategrey;
  margin: 0;
  height: 100%;
  overflow: hidden;
}

body {
  margin: 0;
  height: 100%;
  overflow: hidden;
}

body {
  overflow: auto;
}

.header {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  color: white;
}

.main-title {
  font-size: 60px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.score-board {
  width: 200px;
  font-size: 20px;
}

.current-score {
  /* display: inline-block; */
  color: orange;
}

.best-score {
  display: inline-block;
  color: yellowgreen;
}

.card-container {
  width: 100%;
  display: grid;
  padding: 15px 20px;
  margin-top: 15px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 270px));
  justify-content: space-around;
  column-gap: 10px;
  row-gap: 20px;
}

.card {
  text-align: center;
  color: white;
  outline: none;
  cursor: pointer;
  font-size: 19px;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: 8px;
  font-weight: 800;
  letter-spacing: 1px;
}

img {
  display: block;
  margin: 10px auto;
  max-width: 100%;
  max-height: 100%;
  border-radius: 15px;
}

figcaption {
  margin-top: 15px;
}